.ant-table-body{
	display: grid;
	grid-template-rows: 5fr;
	grid-row-gap: 0.5fr;
	justify-content: center;
	text-align: center;
}

/*行背景色*/ 
.ant-table-thead th {
    text-align:center ! important;
    background: #ff4874 !important; 
    margin:5px;
    /* border: solid 1px red !important */
    /*border-top: solid 1px #cdd8ed !important;
    border-bottom: solid 1px #cdd8ed !important;*/
}

.ant-table-thead th div{
  font-weight: bold;
  color:white;
}
.ant-table-thead > tr >th:last-child { 
   border-right: solid 1px #ffffff !important;
}
.ant-table-thead > tr >th:first-child { 
    border-left: solid 1px #ffffff !important;
 }
.ant-table-tbody  > tr>td{
    border-bottom: solid 1px #ffffff !important;
}
.ant-table-tbody > tr > td:last-child{ 
    border-right: solid 1px #ffffff!important;
}
.ant-table-tbody > tr > td:first-child{ 
   border-left: solid 1px #ffffff !important;
}
/* .ant-table-tbody > tr:first-child > td{ 
    border-top: solid 1px #cdd8ed !important;
 } */

.ant-table-tbody  > tr>td{
    text-align:center ! important;
    background-color:#ffffff! important;
}
.ant-table-tbody > tr:nth-child(odd)  > td { 
    text-align:center ! important;
    background: #ffffff  !important; 
}
 
 
/*hover background odd*/
.ant-table-tbody > tr:hover > td { 
  background: #ffffff  !important;
  border-top: solid 1px #ffffff !important;
  border-bottom: solid 1px #ffffff !important;
 
}

.ant-table-tbody > tr:hover >td:first-child  { 
  border-left: solid 1px #ffffff !important;
}
.ant-table-tbody > tr:hover:nth-child(even) > td:last-child{ 
    border-right: solid 1px #ffffff !important;
}
.ant-table-tbody > tr:hover:nth-child(even):last-child > td{ 
   border-bottom: solid 1px #ffffff !important;
}
